import { mapState, mapActions, mapGetters } from 'vuex'

// window.Notebooks = Notebooks

export default {
  data () {
    return {}
  },

  created () {
    this.checkLogin({ path: '/login' })
    this.getNotebooks()
    console.log('显示正常码')
  },

  computed: {
    ...mapGetters(['notebooks'])
  },

  methods: {
    ...mapActions([
      'getNotebooks',
      'addNotebook',
      'updateNotebook',
      'deleteNotebook',
      'checkLogin'
    ]),
    // 该方法是用作去创建一个新的笔记本,这里调用element-ui组件
    onCreate () {
      this.$prompt('输入新笔记本标题', '创建笔记本', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^.{1,30}$/, // 该属性是使用正则判断不超过30个字符串
        inputErrorMessage: '标题不能为空，且不超过30个字符' // 错误的提示，标题不能为空且不超过30个字符
      }).then(({ value }) => {
        // 这里成功就会执行then 操作，把value值传递给vuex中的addNotebook事件，并把笔记本标题value赋值给title。
        // 调用了vuex中的addNotebook mutation
        this.addNotebook({ title: value })
      })
    },

    onEdit (notebook) {
      const title = ''
      this.$prompt('输入新笔记本标题', '修改笔记本', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^.{1,30}$/,
        inputValue: notebook.title,
        inputErrorMessage: '标题不能为空，且不超过30个字符'
      }).then(({ value }) => {
        this.updateNotebook({ notebookId: notebook.id, title: value })
      })
    },
    onDelete (notebook) {
      this.$confirm('确认要删除笔记本吗', '删除笔记本', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteNotebook({ notebookId: notebook.id })
      })
    }
  }
}
