import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {}
  },

  created () {
    this.setUser()
  },

  methods: {
    ...mapActions({
      setUser: 'checkLogin'
    })
  },

  computed: {
    ...mapGetters([
      'username',
      'slug'
    ])
  }
}

